.profile {
  padding-top: 20px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  width: 300px;

  min-height: 200px;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  border-color: white;
  border: 1px solid black;
  overflow: hidden;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 50%;
}

p {
  margin: 0;
  padding: 0;
  margin-top: 10px;
  font-size: 12px;
  font-weight: 700;
  color: grey;
}

.description {
  margin: 0;
  margin-bottom: 20px;
  width: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.name {
  font-size: 16px;
  color: black;
}

.stats {
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 50px;
  display: flex;
  justify-content: space-evenly;
  list-style-type: none;
  font-size: 12px;
  font-weight: 700;
  background-color: rgba(125, 125, 125, 0.1);
}

.stats li {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-top: 1px solid black;
}
.stats li:not(:last-child) {
  border-right: 1px solid rgb(117, 115, 115);
}
.stats li span:first-child {
  font-weight: 400;
}
